@import url('https://fonts.googleapis.com/css2?family=Source+Sans+Pro:wght@200;300;400;600;700;900&display=swap');

*, *::before, *::after {
  box-sizing: border-box;
}

:root {
  --font-family: 'Source Sans Pro', sans-serif;
  --main-bg-color: #141414;
  --background-highlight: #221F1F;
  --background-press: #000;
  --text-base: #fff;
  --text-subdued: #a7a7a7;
  --now-playing-background: rgba(40, 40, 40, 50%);
  --movie-details-buttons: rgba(255, 0, 0, .798);
  --movie-details-buttons-hover: rgba(255, 0, 0, .5);
}

h1, h2, h3, h4, h5, h6, p {
  padding: 0;
  margin: 0;
  color: var(--text-base);
  font-family: var(--font-family);
}

html {
  background-color: var(--background-press);
}

.App {
  width: 100vw;
  height: 100vh;
}

.horizontalContainer {
  background-color: transparent;
  padding-left: .8rem;
}

.card-image {
  width: 11em;
}

.moviesList {
  height: 20.5em;
  overflow-x: scroll;
  white-space: nowrap;
  padding-top: 2em;
  padding-left: 1.4em;
}
.moviesList::-webkit-scrollbar {
  display: none;
}

.indexCard {
  display: inline-block;
  margin-right: 1em;
  z-index: 1;
  overflow: visible;
}

.indexCard:hover {
  /* transform: translateY(-1em); */
  transform: scale(1.25);
  transition: 0.5s;
}

.indexCard > img {
  border-radius: 10px;
}

h1 {
  font-size: 1em;
}

.moviesList h1 {
  font-size: 1.6em;
  overflow-wrap: break-word;
  margin: 0.5em;
}

.horizontalList {
  font-size: 1.6em;
  font-weight: bold;
  color: rgba(255, 0, 0, 0.798);
  width: 40%;
  z-index: -1;
  padding-left: .8em
}

.horizontalList:hover {
  color: rgba(255, 0, 0, 1);
  cursor: pointer;
}